<template>
  <skeleton v-if="skeletonLoading" :nums="3"></skeleton>
  <div v-else>
    <van-pull-refresh class="content" id="BigBox" v-model="isLoading" @refresh="onRefresh">
      <template #pulling><refreshLoading text="下拉刷新"/></template>
      <template #loosing><refreshLoading text="下拉刷新"/></template>
      <template #loading><refreshLoading text="加载中"/></template>
      <template #success><refreshLoading text="加载完成"/></template>
      <div class="no-data" v-if="!loading && list.length === 0">
        <img class="no-top" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/nodata.png"/>
        <div class="no-bottom">暂无订单</div>
      </div>
      <van-list van-clearfix v-model="loading" :finished="finished" :offset="offset" @load="onLoad" class="order-parent">
        <van-row>
          <van-col class="order" v-for="(item) in list" :key="item.num">
            <div class="order-title">
              <span>订单编号：</span>
              <span>{{item.orderSn}}</span>
            </div>
            <div class="order-body" v-for="value in item.orderProducts" :key="value.orderSn" @click="orderClick(value,item)">
              <div class="order-details" v-if="value.orderRepairs">
                <div class="order-details-top">
                  <img class="shop-photo" :src="value.defaultImageUrl | imageToHttps" />
                  <div class="shop-message">
                    <div class="message-left">
                      <div class="message-one">
                        <div>{{value.productFullName || ''}}</div>
                        <div class="message-one-two">{{value.productName}}</div>
                      </div>
                    </div>
                    <div class="message-right">
                      <div class="message-price">￥{{toFixedTwo(value.productAmount)}}</div>
                      <div class="message-num">×{{value.number}}</div>
                    </div>
                  </div>
                </div>
                <div class="order-btn">
                  <div class="order-track" v-if="!(value.stockType === 'STORE' || value.stockType === 'O2O' || value.stockType === 'SYN')" @click.stop="toTrackingOrders(value,item)" :class="(value.stockType === 'STORE' || value.stockType === 'O2O' || value.stockType === 'SYN')?'order-track-right':''">退款详情</div>
                  <div class="track-content" @click.stop="toReturn(value)" v-if="(value.stockType === 'STORE' || value.stockType === 'O2O' || value.stockType === 'SYN')">退款详情</div>
                </div>
              </div>
            </div>
          </van-col>
        </van-row>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import skeleton from '@/components/skeleton'
import refreshLoading from '@/components/refreshLoading'
import personalApi from '@/http/service/index';
export default {
  name: 'repair',
  components: {
    skeleton,
    refreshLoading,
  },
  beforeRouteLeave(to, from, next) {
    let $content = document.querySelector('#app-warp'); // 列表的外层容器
    let scrollTop = $content ? $content.scrollTop : 0;
    console.log('scrollTop', scrollTop)
    from.meta.scrollTop = scrollTop;
    next();
  },
  activated() {
    let scrollTop = this.$route.meta.scrollTop;
    let $content = document.querySelector('#app-warp');
    if ($content) {
      this.$nextTick(() => {
        $content.scrollTop = scrollTop;
      })
    }
  },
  data() {
    return {
      skeletonLoading: true,
      offset: 100,
      loading: false, // 上拉加载
      finished: false, // 上拉加载
      isLoading: false, // 下拉刷新
      scrollTop: 0, // 滚动条距离顶部的距离
      pageIndex: 0, // 默认加载第一页
      pageSize: 5, // 每页的数据数量
      list:
        [
          // {
          //   orderId: 45934019,
          //   source: 'MSTORE',
          //   orderSn: 'D19081916310733387',
          //   memberId: 138306949,
          //   orderStatus: 202,
          //   paymentStatus: 100,
          //   productAmount: 0.01,
          //   orderAmount: 0.01,
          //   consignee: '阿哈',
          //   isCod: 1,
          //   paymentCode: 'cod',
          //   orderType: 0,
          //   relationOrderSn: '',
          //   street: 12024721,
          //   addTime: '2019-08-19 16:31:07',
          //   addTimeStamp: 1566203467,
          //   regionName: '北京 北京 海淀区 八里庄街道',
          //   address: '阿哈一套装',
          //   mobile: '18311395423',
          //   ckCode: '138306949',
          //   canDelete: true,
          //   canClickConfirm: true,
          //   canTrack: true,
          //   mstoreCancel: false,
          //   payOnline: false,
          //   goAssess: false,
          //   drawback: false,
          //   zActivityOrder: 0,
          //   kjtpay: false,
          //   productTotalNo: 1,
          //   totalAmount: 0.01,
          //   totalAmountS: '0.01',
          //   isMyOrder: true,
          //   canConfirm: false,
          //   isRelated: false,
          //   relationOrderAmount: null,
          //   payRelationOrder: false,
          //   relationOrderId: null,
          //   assessed: false,
          //   canCancel: false,
          //   confirmException: false,
          //   orderStatusName: '售后处理',
          //   finalPay: false,
          //   isSingle: true,
          //   isRightService: true,
          //   totalShippingFeeAmount: 0,
          //   alipay: false,
          //   wxpay: false,
          //   isPayCoupon: false,
          //   codConfirmTime: 1566203467,
          //   codOrder: false,
          //   zActivityStatus: null,
          //   orderCouponAmount: null,
          //   payTimeStamp: 0,
          //   orderProducts: [
          //     {
          //       cOrderSn: 'WD192019697586',
          //       orderProductId: 47094099,
          //       isBook: 0,
          //       productId: 14974,
          //       sku: '600000M16',
          //       defaultImageUrl: 'http://cdn21test.ehaier.com:8080/file/59fac4702461088db91854c3.png',
          //       productFullName: 'Haier/海尔 冰箱',
          //       price: 0.01,
          //       priceS: '0.01',
          //       productName: '600000M16',
          //       number: 1,
          //       productAmount: 0.01,
          //       couponCodeValue: 0.00,
          //       orderPromotionAmount: null,
          //       couponAmount: 0,
          //       bigActivity: true,
          //       freeOrder: null,
          //       halfDay: null,
          //       oneDay: null,
          //       orderRepairId: 17386051,
          //       stockType: 'WA',
          //       status: 110,
          //       shippingMode: 'B2B2C',
          //       couponCode: '',
          //       shippingFee: 0.00,
          //       attrName: null,
          //       brandId: 297,
          //       cateId: 2723,
          //       typeId: 28,
          //       productAttribute: 0,
          //       orderRepairs: true,
          //       storeType: 0,
          //       cPaymentStatus: 200,
          //       o2oType: 50
          //     }
          //   ],
          //   orderYwType: 0,
          //   taobaoGroupId: 0,
          //   canCd: false
          // }
        ],
    }
  },
  beforeRouteEnter(to, from, next) {
    if (from.name === 'mine') {
      next(vm => {
        vm.pageIndex = 0
      })
    } else {
      next()
    }
  },
  mounted () {
    this.onLoad()
  },
  methods: {
    /**
     * @description 点击事件统一处理
    */
    orderClick(v, i) {
      if (v.orderRepairs) {
        if (!(v.stockType === 'STORE' || v.stockType === 'O2O' || v.stockType === 'SYN')) {
          this.toTrackingOrders(v, i)
        } else {
          this.toReturn(v)
        }
      }
    },
    /**
     * @description  去退款详情
    */
    toReturn(val) {
      // (stockType == 'STORE' || stockType == 'O2O') 为 社会化/小家电（新流程）退款详情
      // !((stockType == 'STORE' || stockType == 'O2O') 为 大家电（旧流程） 订单追踪
      // 我的退款详情页面
      this.$router.push({ path: '/details', query: { orderProductId: val.orderProductId } })
    },
    /**
     * @description  订单追踪跳转传参
    */
    toTrackingOrders(val, item) {
      this.$router.push({ path: '/refundProcess', query: { orderProductId: val.orderProductId, memberId: item.memberId } })
    },
    /**
     * @description  下拉刷新
    */
    onRefresh() {
      personalApi.getOrderList({
        orderFlag: 1,
        orderStatus: 5, // 售后维权5
        pageIndex: 0,
        pageSize: 5,
        keyword: ''
      }).then(res => {
        this.skeletonLoading = false
        this.isLoading = false
        // console.log(res)
        this.list = res.data.data.orders
      })
      // setTimeout(() => {
      //   this.$toast('刷新成功');
      //   this.isLoading = false;
      // }, 500);
    },
    /**
     * @description  上拉加载
    */
    onLoad() {
      // 判断智家环境
      if (this.$util.isHaierApp()) {
        this.$store.dispatch('handleUpAuth').then(res => {
          if (res) {
            this.getList()
          } else {
            this.$store.dispatch('doLogin')
          }
        }).catch(() => {
          this.$toast.fail('用户信息异常')
        })
      } else {
        this.getList()
      }
    },
    /**
     * 价格保留两位小数
     */
    toFixedTwo(number) {
      const price = Number(number).toFixed(2);
      return price
    },
    /**
     * 列表接口请求
     */
    getList() {
      const data = {
        orderFlag: 1,
        orderStatus: 5, // 售后维权5
        pageIndex: this.pageIndex,
        pageSize: 5,
        keyword: ''
      }
      personalApi.getOrderList(data).then(res => {
        this.skeletonLoading = false
        if (res.data.success) {
          this.pageIndex = this.pageIndex + 1
          this.list = this.list.concat(res.data.data.orders)
          // 加载状态结束
          this.loading = false;
          if (this.list.length >= res.data.data.countResult) {
            this.finished = true
          }
        } else {
          this.$toast.fail(res.data.data.message);
        }
      })
    }
  }
}
</script>
